.title {
	color: #ff4411;
	font-size: 48px;
	/* font-family: 'Signika', sans-serif; */
	font-family: 'Trocchi', sans-serif;
	text-shadow: 2px 2px black;
}

body,
html {
	height: 100%;
	width: 100%;
	margin: 0;
	padding: 0;
}

body {
	margin: 0;
	background-color: grey;
	background-image: url(/dota-2-autochess-builder/static/media/DonkeyCourier.7edc3987.jpg);
	background-attachment: fixed;
	height: 100%;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

.textStyle {
	padding: 2px;
	background-color: black;
	display: flex;
}

.cardWrapperStyle {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: flex-start;
}

.cardStyle {
	max-width: 80px;
	margin: 4px;
	margin-top: 0px;
	padding: 0px;
	background-color: black;
}

.overflowStyle {
	flex: 1 1;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}

.corner {
	position: absolute;
	top: 0px;
	left: 0px;
	padding: 0;
	max-width: 50%;
	max-height: 50%;
}

.iconStyle {
	width: 10px;
	height: 100%;
	flex: 1 1;
}

.iconContainer {
	display: flex;
}

.rosterFlexContainer {
	/* background-color: lightblue; */
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 1em;
	width: 100%;
}

.rosterFlexTitle {
	flex-grow: 2;
}

.reset {
	margin-left: 20px;
	justify-content: flex-end;
}

.unitCountBoxContainer {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}

.unitBox {
	width: 50px;
	position: relative;
}

.classImg {
	width: 50px;
	height: 50px;
}

.searchIcon {
	margin-right: 1em;
}

